import React, { useLayoutEffect } from "react";

// import BannerVideo from "../../../../assets/images/home/interactive-eq-banner-video.png";
import { BulkImg } from "../../../../pages/BulkImg";

import "./Banner.scss";
import Button from "../../../UIComponents/Buttons/Buttons";
import IconSet from "../../../UIComponents/IconSet/IconSet";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import ScrollAnimation from "react-animate-on-scroll";
gsap.registerPlugin(ScrollTrigger);

const HomeBanner = () => {
  useLayoutEffect(() => {
    let cty = gsap.context(() => {
      ScrollTrigger.create({
        trigger: ".ieq-banner",
        id: "pin-section",
        // markers: true,
        scrub: true,
        start: "50px top",
        end: "200% top",
        // pinSpacing: true,
        pinSpacer: true,
        anticipatePin: 1,
        pin: true,
      });

      gsap.to(".ieq-banner__content", {
        scale: 0.8,
        opacity: 0,
        y: "-50px",
        immediateRender: false,
        scrollTrigger: {
          trigger: ".ieq-banner__content",
          // markers: true,
          id: "banner-content",
          scrub: false,
          start: "170% top",
          end: "1200% top",
          toggleActions: "play none none reverse",
        },
      });
    });

    const canvas = document.getElementById("hero-lightpass");
    const context = canvas.getContext("2d");

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    const frameCount = 594;
    // const currentFrame = (index) =>
    //   `https://www.apple.com/105/media/us/airpods-pro/2019/1299e2f5_9206_4470_b28e_08307a42f19b/anim/sequence/large/01-hero-lightpass/${(
    //     index + 1
    //   )
    //     .toString()
    //     .padStart(4, "0")}.jpg`;

    const images = [];
    const airpods = {
      frame: 0,
    };

    for (let i = 0; i < frameCount; i++) {
      const img = new Image();
      img.src = BulkImg[i];
      images.push(img);

      // console.log(BulkImg[i]);s
    }

    gsap.to(airpods, {
      frame: frameCount - 1,
      snap: "frame",
      ease: "none",
      scrollTrigger: {
        scrub: 3,
        start: "50px top",
        end: "1500px top",
        // markers: true,
        id: "video-animation",
        // trigger: ".ieq-bqnner",
      },
      onUpdate: render, // use animation onUpdate instead of scrollTrigger's onUpdate
    });

    images[0].onload = render;

    function render() {
      context.clearRect(0, 0, canvas.width, canvas.height);
      context.drawImage(
        images[airpods.frame],
        0,
        0,
        canvas.width,
        canvas.height
      );
    }

    return () => cty.revert();
  });
  return (
    <section className="ieq-banner">
      {/* <div className="container"> */}
      <div className="ieq-banner__content">
        <ScrollAnimation
          animateOnce={true}
          offset={-100}
          animateIn="animate__fadeInUp"
          animatePreScroll
        >
          <h1 className="display-3 text-center">
            Hiring technology that accounts for humanity.
          </h1>
        </ScrollAnimation>
        <ScrollAnimation
          animateOnce={true}
          offset={-100}
          animateIn="animate__fadeInUp"
          animatePreScroll
        >
          <Button size="large" tertiary>
            Watch a Demo
            <IconSet bg="bg" iconName="play" />
          </Button>
        </ScrollAnimation>
        {/* <div className="ieq-banner__video">
          <img src={BannerVideo} alt="interactive eq banner video animation" />
        </div> */}
      </div>
      <div className="w-100vw">
        <ScrollAnimation
          animateOnce={true}
          offset={-100}
          animateIn="animate__fadeInUp"
          animatePreScroll
        >
          <canvas id="hero-lightpass"></canvas>
        </ScrollAnimation>
      </div>

      {/* </div> */}
    </section>
  );
};

export default HomeBanner;
